import React from 'react'
import ReactDOM from 'react-dom'
import ContactBtn from '../Contact'
import Form from '../Form'
import { render } from '@testing-library/react'

import {createStore,applyMiddleware} from 'redux'
import thunk from 'redux-thunk'
import {reducer} from '../../../redux/reducers'
import {Provider} from 'react-redux'


const store = createStore(reducer,applyMiddleware(thunk))


const props = {
	name:'Guy',
	email:'GuyTheGreat@gmail.com',
	message:'this is the message',
	send:jest.fn()
}

test('Form renders with out crashing', async ()=>{
	const {screen,getByText} = render(<Provider store={store}><Form {...props}/></Provider>)
		const form = screen.getByText('form')
		console.log(form)
		// expect()

})		



